import React from "react";

import {
  MDBCarousel,
  MDBCarouselInner,
  MDBCarouselItem,
  MDBView,
  MDBContainer
} from "mdbreact";



function HomeCarousel() {
  return (
    <MDBContainer fluid className="px-0">
    <MDBCarousel
      activeItem={1}
      length={2}
      showControls={true}
      showIndicators={true}
      className="z-depth-1" //z depth 가 그림자 넣어주는거임
    >
      <MDBCarouselInner>
        <MDBCarouselItem itemId="1">
          <MDBView>
            <img
              src="http://www.suvincos.com/sh_img/index/main_banner/main_banner_01.png"
              alt="First slide"
            />
            
          </MDBView>
        </MDBCarouselItem>
        <MDBCarouselItem itemId="2">
          <MDBView>
            <img
              src="http://www.suvincos.com/sh_img/index/main_banner/main_banner_02.png"
              alt="Second slide"
            />
            
          </MDBView>
        </MDBCarouselItem>

      </MDBCarouselInner>
    </MDBCarousel>
  </MDBContainer>
  );
}


export default HomeCarousel;